import { createWebHistory, createRouter } from "vue-router";

import IndexComp from "@/components/indexComp.vue";

import introductionComp from "@/components/IntroductionComp.vue";
import SelfIntroComp from "@/components/SelfIntroComp.vue";
import CharacterComp from "@/components/CharacterComp.vue";
import MotiveComp from "@/components/MotiveComp.vue";
import BackGroundComp from "@/components/BackGroundComp.vue";

import ProjectComp from "@/components/ProjectComp.vue";
import AirflowPComp from "@/components/AirflowPComp.vue";
import PythonPCommp from "@/components/PythonPComp.vue";
import ReactPComp from "@/components/ReactPComp.vue";
import SparkPComp from "@/components/SparkPComp.vue";

import SkillComp from "@/components/SkillComp.vue";
import SkillsComp from "@/components/SkillsComp.vue";
import CertificateComp from "@/components/CertificateComp.vue";

import ChronologyComp from "@/components/ChronologyComp.vue";
import AlbaComp from "@/components/AlbaComp.vue";
import CareerComp from "@/components/CareerComp.vue";
import EducationComp from "@/components/EducationComp.vue";

import EtcComp from "@/components/EtcComp.vue";
import DataEngineerComp from "@/components/DataEngineerComp.vue";
const routes = [
    {
        path: "/",                         // index Component
        component: IndexComp,
    },
    {
        path: "/introduction",
        component: introductionComp,
    },
    {
        path: "/introduction/SelfIntro",
        component: SelfIntroComp
    },
    {
        path: "/introduction/Character",
        component: CharacterComp
    },
    {
        path: "/introduction/Motive",
        component: MotiveComp
    },
    {
        path: "/introduction/BackGround",
        component: BackGroundComp
    },
    {
        path: "/Project",
        component: ProjectComp
    },
    {
        path: "/Project/AirflowP",
        component: AirflowPComp
    },
    {
        path: "/Project/PythonP",
        component: PythonPCommp
    },
    {
        path: "/Project/ReactP",
        component: ReactPComp
    },
    {
        path: "/Project/SparkP",
        component: SparkPComp
    },
    {
        path: "/Skill",
        component: SkillComp
    },
    {
        path: "/Skill/Skills",
        component: SkillsComp
    },
    {
        path: "/Skill/Certificate",
        component: CertificateComp
    },
    {
        path: "/Chronology",
        component: ChronologyComp
    },
    {
        path: "/Chronology/Alba",
        component: AlbaComp
    },
    {
        path: "/Chronology/Career",
        component: CareerComp
    },
    {
        path: "/Chronology/Education",
        component: EducationComp
    },
    {
        path: "/Etc",
        component: EtcComp
    },
    {
        path: "/Etc/DataEngineer",
        component: DataEngineerComp
    },
];
const router = createRouter({    // 라우트 생성
    history: createWebHistory(),
    routes,
});
export default router;

